
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import '../css/toptitle.scss';
class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            title: '',
            titlelist: [
                { name: '基本资料', url: '/' },
                // { name: '专业技能', url: '/about' },
                { name: '工作经历', url: '/experience' },
                { name: '项目经验', url: '/projects' },
                { name: '自我评价', url: '/myhoke' }
            ],
            count: 0
        }
    }
    componentDidMount() {
        this.props.history.push('/');
        // this.setState({
    }

    render() {
        return (
            <div className="header" style={{backgroundColor:this.state.count==0?'#0c2738':''}}>
                <div className="headerLeft"></div>
                <div className="headerRight">
                    {this.state.titlelist.map((item, index) => (
                        <div className="headerRightItem" key={index}>
                            <div className="btn"
                                onClick={() => {
                                    this.props.history.push(item.url);
                                    this.setState({
                                        count: index
                                    })
                                }}
                                style={{
                                    boxShadow: this.state.count === index ? 'inset 2px 2px 8px #ff1f71,inset -2px -2px 8px #ff1f71' : '',
                                    borderRadius: '30px'
                                }}><a href="#" >{item.name}</a></div>
                        </div>
                    ))}
                </div>
            </div>
        );
    }
}

export default withRouter(Header)