import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { router } from './utils/router';
import Header from './components/toptitle';
import 'antd/dist/reset.css';
function App() {
  return (
    <Router>
      <Header />
      <Switch>
        {router.map((item,index) => (<Route key={index} path={item.path} exact component={item.component} />))}
      </Switch>
    </Router>
  );
}
export default App;
