import React, { Component } from 'react';
import "../css/experience.scss"

class Experience extends Component {
  constructor(props) {
    super(props);
    this.state = {
      experience: [
        {
          date: '2020、10-2021、7、2',
          company: '三早健康科技（杭州）有限公司',
          position: '前端开发实习',
          description: '负责公司微信小程序和后台管理系统的开发和维护，使用vue、uni-app、jQuery、layui框架、es6、less、npm、git等前端技术，完成公司内部管理系统的前端开发工作',
        },
        {
          date: '2021、7-2022、7、5',
          company: '汇智道晟(杭州)软件有限公司',
          position: '前端开发工程师',
          description: '负责公司外包的物业app和业主app，还有物业后台管理系统的前端开发工作，使用vue、Avue、axios、vue-router、uni-app、js、es6、sass、gitlab等前端技术，开发外包的项目',
        },
        {
          date: '2022、7-2023、8、31',
          company: '杭州佳跑科技有限公司',
          position: '前端开发工程师',
          description: '负责公司健身app和公司官网还有内部管理系统的前端开发工作，使用vue、axios、uni-app、es6、react、react-router-dom、sass、gitee、gitlab、antd UI框架等技术开发公司的项目',
        }
      ],
      cuont: 0,
      timeset: null,
      show: true
    }
  }
  componentDidMount() {
    let { timeset } = this.state;
    this.setState({
      timeset: setInterval(() => {
        if (this.state.show) {
          if (this.state.cuont < this.state.experience.length - 1) {
            this.setState({ cuont: this.state.cuont + 1 })
          } else {
            this.setState({ cuont: 0 })
          }
        }
        // 定时器
      }, 2000)
    })
  }
  componentWillUnmount() {
    clearInterval(this.state.timeset)
  }
  render() {
    let { experience, cuont } = this.state;
    return (
      <div className="experienceBox">
        <div className="side left-side">
          {this.state.experience.map((item, index) => {
            return (
              <div className="card"
                key={index}
                style={{
                  backgroundColor: index === cuont ? 'rgba(255,255,255,0.2)' : '',
                  // color: index === cuont ? '#000' : '#fff'
                }}
                onClick={() => {
                  this.setState({
                    cuont: index,
                    show: false
                  })
                  setTimeout(() => {
                    this.setState({
                      show: true
                    })
                  }, 3000)
                }}>
                <div className="card-header">
                  <div className="card-title">{item.company}</div>
                  <div className="card-date">{item.date}</div>
                </div>
              </div>
            )
          })}
        </div>
        <div className="side center-side">
          <div className="card">
            <div className="center-box">
              <div className="center-item">{experience[cuont].company}</div>
              <div className="center-item">{experience[cuont].date}</div>
              <div className="center-item">{experience[cuont].position}</div>
              <div className="center-item">{experience[cuont].description}</div>
            </div>
          </div>
        </div>
        <div className="side right-side">
          {/* <div className="card"></div> */}
        </div>
      </div>
    );
  }
}

export default Experience